import React,{Component} from 'react';
import {Card, TabBar, WhiteSpace, WingBlank} from "antd-mobile";
import ShareActivityShowCard from "../ShareActivityShowCard";
import ActivityShowCard from "../ActivityShowCard";
import axios from 'axios';

class FirstTabContent extends Component{


    constructor(props){
        super(props);
        this.state = {
            isShare:false,
            //
            myActivity:[],
            shareActivity:''
        }
    }

    componentWillMount(){
        // console.log(this.props.history);
    }

    componentDidMount(){
        // 
        if(window.location.href.match('/share/')){

            this.setState({
               isShare:true
            });

            let array = window.location.pathname.split('/');
            let activityId = array.pop();
            console.log(activityId);

            axios.defaults.withCredentials=true;
            axios.get('http://118.31.34.71:9997/share/'+activityId).then((data)=>{
                console.log(data.data.data);
                this.setState({
                    shareActivity: data.data.data
                })
            });


            console.log('')
        }else{

            console.log('')
            axios.defaults.withCredentials=true;
            axios.get('http://118.31.34.71:9997/activity/list').then(data=>{
                // console.log(data);
                let myActivity = data.data.data;
                // console.log(myActivity);
                this.setState({
                    myActivity
                })
            }).catch((err)=>{
                //
                console.log('');
            })
        }
    }

    render(){

        return (<div>
            {/**/}
            {/*0*/}

            <div style={{display: this.state.isShare === true ?'block':'none' }}>
                <ShareActivityShowCard id={this.state.shareActivity.id}
                                       topic={this.state.shareActivity.topic}
                                       location={this.state.shareActivity.location}
                                       start={this.state.shareActivity.start}
                                       end={this.state.shareActivity.end}
                                       history={this.props.history}

                />
            </div>


            <div style={{display:this.state.isShare === false && this.state.myActivity.length === 0 ?'block':'none' }}>
                {/*<div></div>*/}

                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <WhiteSpace size="lg" />
                    <Card>
                        <Card.Header title="~" style={{fontWeight:'bolder'}}/>
                        <Card.Body>
                            <div>
                                <span>@_@</span>
                            </div>
                        </Card.Body>
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>

            </div>
            <div style={{display:this.state.isShare === false && this.state.myActivity.length !== 0 ?'block':'none' }}>

                {/*<ActivityShowCard status='OWNER' topic='' location='' start='20190114' end='20190214'/>*/}
                {/*<ActivityShowCard status='MEMBER' topic='' location='' start='20190114' end='20190214'/>*/}
                {/*<ActivityShowCard status='PREMEMBER' topic='' location='' start='20190114' end='20190214'/>*/}
                {/*<ActivityShowCard status='EXITMEMBER' topic='' location='' start='20190114' end='20190214'/>*/}
                {/*<ActivityShowCard status='UNMEMBER' topic='' location='' start='20190114' end='20190214'/>*/}

                {this.state.myActivity.map( (item) =>{
                    return <ActivityShowCard key={item.activityId}
                                             history={ this.props.history }
                                             isWhichPage = 'main'
                                             id={item.activityId}
                                             status={item.status}
                                             topic={item.activityVO.topic}
                                             location={item.activityVO.location}
                                             start={item.activityVO.start}
                                             end={item.activityVO.end} />
                })}

            </div>
                </div>)
    }

}

export default FirstTabContent;